import Vue from 'vue';
import VueRouter from 'vue-router';
import Core from "@/components/Core.vue";

Vue.use(VueRouter);

const routes = [
    {
        path: '/',
        name: 'core',
        component: Core,
    },
];

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes,
});

export default router;
